<template>
  <div class="problems">
    <h1>Choose Your Summer Job</h1>
    <p>
      假设你想要找一份暑期工，下面给了你一些虚构的暑期工作的条件。
      给出的信息包含通勤时间（可能没有），时薪，工作时间（平均到每天），以及你对这个工作的感兴趣程度（50%代表能接受；100%代表非常感兴趣）
      <br />
      请选择你对这份工作的感觉/想法。
      <br />
      我们信任接受调查的人。请<strong>回答你真实的想法</strong>，并且<b>不要把任何题目留空。</b>
    </p>
    <div class="personal">
      你的年级：
      <select v-model="personal.grade">
        <option :value="0">9年级（初三）</option>
        <option :value="1">10年级（高一）</option>
        <option :value="2">11年级（高二）</option>
        <option :value="3">12年级（高三）</option>
      </select>
      <br />
      你的性别：
      <select v-model="personal.gender">
        <option value="F">女</option>
        <option value="M">男</option>
      </select>
      <br />
      你是否感到“缺钱”？
      <select v-model="personal.poor">
        <option :value="0">Yes</option>
        <option :value="1">No</option>
      </select>
      <br />
      你的QQ号：<input v-model="personal.qq" type="number">
      <hr />
    </div>
    <div class="problem" v-for="(i, ix) in data" :key="ix">
      <div class="commute"><strong>通勤时间为 </strong> {{ i.commute }} 分钟</div>
      <div class="wage"><strong>时薪是 </strong>¥{{ i.wage * 2 }} 每小时</div>
      <div class="hours"><strong>工作时间</strong>平均每天 {{ i.hours }} 小时</div>
      <div class="interested"><strong>你对这个工作的内容</strong> {{ i.interested }}% 感兴趣</div>
      <div class="likely">你觉得你有多倾向于选择这份工作？
        <select v-model="i.likeliness">
          <option :value="0">就它了</option>
          <option :value="1">看起来不错</option>
          <option :value="2">还可以接受</option>
          <option :value="3">不太感兴趣</option>
          <option :value="4">不可能</option>
        </select>
      </div>
      <hr />
    </div>
    <button id="submit" @click="submit">完成</button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Problems',
  data: () => ({
    personal: {
      grade: undefined,
      gender: undefined,
      poor: undefined,
      qq: undefined,
    },
    data: [{},{},{},{},{},{},{},{}].map(() => ({
      commute: Math.round(Math.random() * 6) * 10,
      wage: Math.round(Math.random() * 27) + 3,
      hours: Math.round(Math.random() * 3) + 1,
      interested: Math.round(Math.random()* 50) + 50,
      likeliness: undefined,
    }))
  }),
  methods: {
    submit() {
      axios.post('http://47.110.138.116/', {personal: this.personal, data: this.data})
      alert('感谢作答，请关闭问卷')
    }
  }
}
</script>

